.menu-mob {
  width: 100vw;
  height: 100vh;
  z-index: 2;
  opacity: 1;
  visibility: visible;
  transition: transform 1000ms var(--timing-function);
  background-color: #e17992;
  padding-top: 58px;
  position: absolute;
  overflow-y: scroll;
  transform: translateX(0);
}

.menu-mob.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  display: none;
  transform: translateX(100%);
}

